import React, { Component } from 'react';

export class SearchBar extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  _scrollTo() {
   let $ = require('jquery');
   $("body,html").animate({
        scrollTop: $(".sign-up-form-container").offset().top
    }, 500);
  }
  render() {
    return (
      <div className="searchbar">
        <ul>
          <li className="searchbar-search"><input type="text" placeholder="Search items"/></li>
          <li className="searchbar-collection">
            <select>
              <option>All Categories</option>
            </select>
          </li>
          <li className="searchbar-location"><input type="text" placeholder="San Francisco"/></li>
        </ul>
      <div className="search-submit-container">
        <div className="search-submit-button">
          <p onClick={this._scrollTo}>START</p>
        </div>
      </div>
      </div>
    );
  }
}


