import React, { Component } from 'react';
import flowGraphic from '../../images/icon/flow-graphic.png'
import shareFlow from '../../images/icon/share-flow.png'

export class ServiceContent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentTabStatus: 'active-tab-1'
    };
  }
  _switchTab(selectTab){
    this.setState({
      currentTabStatus: selectTab
    })
  }

  _switchContent(selectTab){

    switch(selectTab){
      case 'active-tab-1':
        return (
          <div className="our-service-content-article">
            <div className="flow-graphic">
              <img src={flowGraphic} width="1220"/>
            </div>

            <div className="article-content">
              <p>
                iiShare offers a secure, reliable and convenient platform for renting items around the world. People can share their items easily with only just tap on smart phone to take a picture, say something about the items and publishes. Simple, fast with no pain.
              </p>
              <p>
                Searching for some stuffs to rent ? You can just take a picture, iiShare will tell you who has the item you want and have a great chat to make an offer, payment service supports multiple channels for whatever you want and wherever you from. iiShare also cooperates with trusted third-party delivery system in China and United States to make things more easier and much faster. In order to protect your items, iiShare has complete mechanism to handle insurance, protect your rights and items with suitable programs. Every user has certification signs with many channels, authentication mechanism can bring safety to every transaction.
              </p>
              <p>
                By renting your stuffs out, make more friends around the world, earn extra money and have fun. Create your own store and help others. Start from now, tap to list and tap to share
              </p>
            </div>
          </div>
        );
      break;
      case 'active-tab-2':
        return (
          <div className="our-service-content-article">
            <div className="flow-graphic">
              <img src={shareFlow} width="1024"/>
            </div>
            <div className="article-content">
              <p>
                iiShare offers a secure, reliable and convenient platform for renting items around the world. People can share their items easily with only just tap on smart phone to take a picture, say something about the items and publishes. Simple, fast with no pain.
              </p>
              <p>
                Searching for some stuffs to rent ? You can just take a picture, iiShare will tell you who has the item you want and have a great chat to make an offer, payment service supports multiple channels for whatever you want and wherever you from. iiShare also cooperates with trusted third-party delivery system in China and United States to make things more easier and much faster. In order to protect your items, iiShare has complete mechanism to handle insurance, protect your rights and items with suitable programs. Every user has certification signs with many channels, authentication mechanism can bring safety to every transaction.
              </p>
              <p>
                By renting your stuffs out, make more friends around the world, earn extra money and have fun. Create your own store and help others. Start from now, tap to list and tap to share
              </p>
            </div>
          </div>
        );
      break;
    }
  }


  render() {
    let tabSelect = "tab-select-actived " + this.state.currentTabStatus;
    let tabContent = this._switchContent( this.state.currentTabStatus )
    return (
      <div className="our-service-construct">
        <div className="our-service-contentbox">
          {/* tab */}
          <div className="our-service-tabbox">
            <div className="our-service-tab our-service-tab-1" onClick={this._switchTab.bind(this,'active-tab-1')}>HOW TO RENT</div>
            <div className="our-service-tab our-service-tab-2" onClick={this._switchTab.bind(this,'active-tab-2')}>HOW TO SHARE</div>
          </div>
          {/* tab underline */}
          <div className="tab-select-line">
            <div className={tabSelect}></div>
          </div>
          {/* content */}
          <div className="our-service-content">
            {tabContent}
          </div>
          {/*--------------------*/}
        </div>
      </div>
    );
  }
}


