import React, { Component } from 'react';

export class FloatSearchBar extends Component {
  render() {
    return (
      <div className={this.props.status}>
        <div className="fixed-search-bar-container">
          <ul>
            <li className="fixed-search-bar-search"><input type="text" placeholder="Search items"/></li>
            <li className="fixed-search-bar-collection">
              <select>
                <option>All Categories</option>
              </select>
            </li>
            <li className="fixed-search-bar-location"><input type="text" placeholder="San Francisco"/></li>
          </ul>
          <div className="fixed-search-bar-submit"> Start </div>
        </div>
      </div>
    )
  }
}


