import React, { Component } from 'react';
export class Footer extends Component {
  _scrollTo() {
   let $ = require('jquery');
   $("body,html").animate({
        scrollTop: $(".sign-up-form-container").offset().top
    }, 500);
  }
  render(){
    let nav = [];
    let navConfig = this.props.menu;
    for( let alias in navConfig ){
      nav.push(<li key={alias} ><a href={navConfig[alias]}>{alias.toUpperCase().replace('_',' ')}</a></li>)
    }
    return(
      <footer>
        <div className="footer-content">
          <div className="footer-column">
            <div className="footer-brand" >LOGO</div>
            <ul className="footer-navigation">
              {nav}
            </ul>
          </div>
          <hr />
          <div className="footer-column">
            <ul className="footer-social">
              <li className="social-twitter"><a onClick={this._scrollTo} ></a></li>
              <li className="social-facebook"><a onClick={this._scrollTo} ></a></li>
              <li className="social-instagram"><a onClick={this._scrollTo} ></a></li>
              <li className="social-pinterest"><a onClick={this._scrollTo} ></a></li>
              <li className="social-evernote"><a onClick={this._scrollTo} ></a></li>
            </ul>
            <div className="footer-reserved"> All rights reserved 2016 iiShare Inc. </div>
          </div>
        </div>
      </footer>
    )
  }
}




