import React, { Component } from 'react';
import { Navigation } from './navigation';
import { SearchBar } from './searchbar';
import iosDownload_png from '../../images/icon/apple-download.png';
import googleDownload_png from '../../images/icon/google-download.png';


export class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  _scrollTo() {
   let $ = require('jquery');
   $("body,html").animate({
        scrollTop: $(".sign-up-form-container").offset().top
    }, 500);
  }
  render() {
    return (
      <header className="landing-section">
        <Navigation menu={this.props.menu}/>
        <div className="landing-title">
          <p>Share and Rent Everything from Everywhere</p>
        </div>
        <SearchBar/>
        <div className="application-download-container">
          <div className="app-download">
            <a onClick={this._scrollTo} ><img src={iosDownload_png} width="172"/></a>
            <a onClick={this._scrollTo} ><img src={googleDownload_png} width="172"/></a>
          </div>
        </div>
        <div className="landing-collection-container">
          <a onClick={this._scrollTo} className="landing-collection-1 landing-collection "><p>RENTING GURUS</p></a>
          <a onClick={this._scrollTo} className="landing-collection-2 landing-collection high-light"><p>TOP 10 ITEMS</p></a>
          <a onClick={this._scrollTo} className="landing-collection-3 landing-collection"><p>DAILY NEWS</p></a>
        </div>
      </header>
    );
  }
}






