import React, { Component } from 'react';
import { ServiceContent } from './service_content';


export class Content extends Component {
  render() {
    return (
      <section>
        <div className="our-service-container">
          <div className="our-service-title">
            <p>Our Service</p>
          </div>
        <ServiceContent />
        </div>
        <div className="feature-container">
          <div className="feature-info">
            <p>Why iiShare</p>
          </div>
          <div className="feature-info-content">
            <ul className="feature-list">
              <li>
                <h3>TOUCH TO LIST</h3>
                <p>In iiShare, you can find items you want with just a single tab to take a picture or speak out the item you are targeting and iiShare will use our high imagine recognition tech to grab all the things out.  Easy to use and easy to find.</p>
              </li>
              <li>
                <h3>TOUCH TO SHARE</h3>
                <p>Want to share your item with just one step ? iiShare can do that. Tab to take a picture for your item and describe the details then iiShare will publish it autometically. Smart and user friendly.</p>
              </li>
              <li>
                <h3>SECURITY AND GUARANTEE</h3>
                <p>iiShare guarantees that every transaction is secure and safe with high level security standard. iiShare also guarantees that every item on our platform will have insurance to protect your property. iiShare always be the back for every user.</p>
              </li>
            </ul>
            <div className="feature-img" />
          </div>
        </div>
        {/* -------------------- */}
        <div className="sign-up-container">
          <div className="sign-up-info">
            <p>Join iiShare for <b>FREE</b> to start your
            renting journey ever before</p>
          </div>
          <div className="sign-up-form-container">
            <div className="sign-up-form">
              <input type="text" className="sign-name" placeholder="Your Name"/>
              <input type="text" className="sign-email" placeholder="Email Address"/>
              <div className="sign-up-submit">Sign Up</div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}


