import React, { Component } from 'react';

export class Navigation extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentWillmount() {}
  _scrollTo() {
   let $ = require('jquery');
   $("body,html").animate({
        scrollTop: $(".sign-up-form-container").offset().top
    }, 500);
  }
  render() {
    let nav = [];
    let navConfig = this.props.menu;
    for( let alias in navConfig ){
      nav.push(<li key={alias} onClick={this._scrollTo}><a href={navConfig[alias]}>{alias.replace('_',' ')}</a></li>)
    }
    return (
      <div className="navigation-section">
        <div className="navigation-bar">
          <div className="navigation-brand">LOGO</div>
          <div className="navigation-menu">
            <ul>{nav}</ul>
          </div>
        </div>
      </div>
    );
  }
}



