import React, { Component } from 'react';
import Waypoint from 'react-waypoint';
import { Header } from './component/header';
import { Content } from './component/section';
import { Footer } from './component/footer';
import { FloatSearchBar } from './component/float_searchbar';

const menu = {
  Service:'#Service',
  Download:'#Download',
  Market:'#Market',
  Blog:'#Blog',
  About:'#About',
  Sign_Up:'#Sign_Up'
}


export class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      floatSearchBar: false
    };
  }
  componentDidMount(){
    document.getElementsByTagName("body")[0].scrollTop > 800 ? this._controllFloatSearchBar(true) : '';
  }
  _controllFloatSearchBar(status){
    console.log(status)
    this.setState({
      floatSearchBar: status
    })
  }

  _controllFloatSearchBarStatus(){
    if(this.state.floatSearchBar){
      return 'active'
    }else{
      return ''
    }

  }


  render() {
    let SearchBarStatus = "fixed-searchbar " + this._controllFloatSearchBarStatus();
    return (
      <div className="main">
        <FloatSearchBar status={SearchBarStatus}/>
        <Waypoint
          onEnter={this._controllFloatSearchBar.bind(this,false)}
          onLeave={this._controllFloatSearchBar.bind(this,true)}
          threshold={1.2}
        />
        <Header menu={menu}/>
        <Content />
        <Footer menu={menu}/>
      </div>
    );
  }
}


